// Database
import PouchDB from 'pouchdb'
import { from, of } from 'rxjs'
import { ajax } from "rxjs/ajax";
import { map, mergeMap, catchError, mapTo, tap } from 'rxjs/operators'

interface Record {
  _id: string
  _rev: string
  updatedTime: number
  data: any
  expired: boolean
}

interface MethodSet {
  getChart: object
}

const constructor = (schema = 'investment-insight') => {
  const db = new PouchDB('investment-insight', { auto_compaction: true })
  const baseUrl = 'https://api.iextrading.com/1.0'

  const oneMinute = 60 * 1000
  const cache = {
    tenMinutes: 10 * oneMinute,
    fifteenMinutes: 15 * oneMinute,
    oneHour: 60 * oneMinute,
    oneDay: 24 * 60 * oneMinute,
    oneHundredYear: 100 * 365 * 24 * 60 * oneMinute
  }

  const getCache = (table: string, cacheTime: number) => {
    return from(db.get(table))
      .pipe(catchError(err => {
        return of({})
      }))
      .pipe(map((doc: Record) => {
        if (!doc._id || !doc.updatedTime) {
          return {}
        }

        const timeDifference = Date.now() - doc.updatedTime
        if (timeDifference > cacheTime) {
          return {
            expired: true,
            _rev: doc._rev
          }
        }

        return doc
      }))
  }

  const setCache = (table: string, data: any) => {
    const params = {
      _id: table,
      data,
      updatedTime: Date.now(),
    }

    return from(db.put(params))
      .pipe(
        map(doc => Object.assign(doc, { data }))
      )
  }

  const sync = (table: string, url: string, cacheTime = cache.oneDay) => {
    return of(table).pipe(
      mergeMap(table => getCache(table, cacheTime)),
      mergeMap((doc: Record) => {
        if (doc.expired) {
          return from(db.remove(table, doc._rev)) 
        }

        return of(doc)
      }),
      mergeMap((doc: Record) => {
        if (!doc.data) {
          return ajax.get(`${baseUrl}/${url}`).pipe(
            map(res => res.response),
            mergeMap((data: any) => setCache(table, data)),

          )
        }

        return of(doc)
      })
    )
  }

  const getSymbols = () => {
    const table = 'ref_data_symbols'
    const url = `ref-data/symbols`
    return sync(table, url)
  }

  const getCompany = (symbol: string) => {
    const table = `${symbol}_company`
    const url = `stock/${symbol}/company`
    return sync(table, url) 
  }

  const getChart = (symbol: string, range = '1y') => {
    const table = `${symbol}_${range}_chart`
    const url = `stock/${symbol}/chart/${range}`
    return sync(table, url)
  }

  const getEarnings = (symbol: string, range = '1y') => {
    const table = `${symbol}_earnings`
    const url = `stock/${symbol}/earnings`
    return sync(table, url)
  }

  const getNews = (symbol: string) => {
    const table = `${symbol}_news`
    const url = `stock/${symbol}/news/last/10`
    return sync(table, url, cache.fifteenMinutes)
  }

  const getKeyStat = (symbol: string) => {
    const table = `${symbol}_keystat`
    const url = `stock/${symbol}/stats`
    return sync(table, url)
  }

  //https://api.iextrading.com/1.0/stock/market/list/gainers
  const gainers = () => {
    const table = `stock_gainers`
    const url = `stock/market/list/gainers?displayPercent=true`
    return sync(table, url, cache.oneHour)
  }

  //https://api.iextrading.com/1.0/stock/market/list/losers
  const losers = () => {
    const table = `stock_losers`
    const url = `stock/market/list/losers?displayPercent=true`
    return sync(table, url, cache.oneHour)
  }

  const getWatchList = () => {
    const table = 'stock_watchlist'
    return from(db.get(table)).pipe(
      catchError(err => {
        return of({})
      })
    )
  }

  const setWatchList = (data: any) => {
    const table = 'stock_watchlist'

    return of(table).pipe(
      mergeMap(table => getCache(table, cache.oneHundredYear)),
      mergeMap((doc: Record) => {
        if (doc._id) {
          return from(db.remove(doc))
        }
        
        return of({})
      }),
      mergeMap(() => setCache(table, data))
    )
  }

  return { 
    getSymbols, 
    getCompany, 
    getChart, 
    getEarnings, 
    getNews, 
    getKeyStat,
    gainers,
    losers,
    getWatchList,
    setWatchList,
  }
}

export default constructor