





































import { Component, Vue } from "vue-property-decorator";
import { Subject, from, of, throwError } from "rxjs";
import { ajax } from "rxjs/ajax";
import { map, mergeMap, catchError, concatMap } from "rxjs/operators";

@Component
export default class Watchlist extends Vue {
  stockdb: any
  watchlists: any
  notice!: string
  errorMessage: string = 'Nothing in watchlist.. let\'s add one from '

  data() {
    return {
      notice: '',
      watchlists: {},
    }
  }

  created() {
    this.stockdb.getWatchList().pipe(
      mergeMap((res: any) => {
        if (!res.data) {
          return throwError(new Error(this.errorMessage))
        }
        this.watchlists = res.data
        return from(Object.keys(res.data))
      }),
      concatMap((symbol: any) => {
        return this.stockdb.getChart(symbol, 'ytd').pipe(
          map((res: any) => res.data),
          map((data: any) => {
            this.watchlists[symbol].lastChart = data[data.length - 1]
            this.watchlists = Object.assign({}, this.watchlists)

            return data
          })
        )
      })
    )
      .subscribe(
        (result: any) => {
          console.log(result)
        },
        (error: Error) => {
          if (error.message) {
            this.notice = this.errorMessage
          }
        }
      )
  }
}
