





















import { Component, Prop, Vue } from 'vue-property-decorator'

interface ChartData {
  change: number
  changePercent: number
  close: number
  date: string
  high: number
  open: number
}

@Component
export default class Chart extends Vue {
  stockdb: any
  symbol!: string
  tickers!: ChartData[]

  data() {
    return {
      symbol: this.$route.params.symbol,
      tickers: [],
    }
  }

  mounted() {
    this.stockdb.getChart(this.symbol).subscribe(
      (result: any) => {
        if (result.data) {
          this.tickers = result.data.slice().reverse()
        }
      },
      (error: Error) => {
        console.error(error)
      }
    )
  }
}
