






























import { Component, Prop, Vue } from 'vue-property-decorator';

interface StockData {
  change: number
  changePercent: number
  companyName: string
  open: number
  high: number
  low: number
  latestPrice: number
  latestTime: string
  latestUpdate: number
  primaryExchange: string
  sector: string
  symbol: string
  week52High: number
  week52Low: number
}

@Component
export default class PerformanceCard extends Vue {
  @Prop() private label!: string
  @Prop() private stockdata!: StockData[]
}
