



























































import { Component, Vue } from 'vue-property-decorator';
import { from } from 'rxjs'
import { ajax } from "rxjs/ajax"
import { map, mergeMap, defaultIfEmpty, catchError } from 'rxjs/operators'

interface Company {
  symbol: string;
  companyName: string;
  exchange: string;
  industry: string;
  website: string;
  description: string;
  CEO: string;
  issueType: string;
  sector: string;
  tags: string[]
}

@Component
export default class KeyStat extends Vue {
  stockdb: any
  symbol!: string
  company! :Company

  data() {
    const symbol = this.$route.params.symbol.toUpperCase()
    return {
      symbol,
      company: {},
      navs: [
        {
          name: 'stocks keystat',
          title: 'Key Statistics'
        },
        {
          name: 'stocks news',
          title: 'News'
        },
        {
          name: 'stocks chart',
          title: 'Chart',
        },
        {
          name: 'stocks earnings',
          title: 'Earnings'
        },
      ],
      externalLinks: [
        {
          name: 'cnbc',
          title: 'CNBC',
          url: ` https://www.cnbc.com/quotes/?symbol=${symbol}`,
        },
        {
          name: 'zacks',
          title: 'Zacks',
          url: `https://www.zacks.com/stock/quote/${symbol}`,
        },
        {
          name: 'yahoo finance',
          title: 'Yahoo Finance',
          url: `https://finance.yahoo.com/quote/${symbol}`,
          icon: 'fab fa-yahoo'
        },
        {
          name: 'cnn money',
          title: 'CNN Money',
          url: `http://money.cnn.com/quote/forecast/forecast.html?symb=${symbol}`,
        }
      ]
    }
  }

  mounted() {
    this.stockdb.getCompany(this.symbol).subscribe(
       (result: any) => {
        this.company = result.data
      },
      (error: Error) => {
        console.error(error)
      }
    )
  }
}
