












import { Component, Vue } from "vue-property-decorator";
import { from, of } from "rxjs";
import { map, mergeMap, toArray } from "rxjs/operators";
import PerformanceCard from '@/components/stocks/PerformanceCard.vue';
const numeral = require('numeral')
const moment = require('moment')

const transformPerformance = (data: any[]) => {
  return from(data).pipe(
    map((res: any) => {
      res.changePercent = numeral(res.changePercent).format('0.000')
      res.latestUpdate = moment(res.latestUpdate).fromNow()

      return res
    }),
    toArray()
  )
}

@Component({
  name: "Leaderboard",
  components: {
    PerformanceCard
  }
})
export default class Leaderboard extends Vue {
  stockdb: any
  symbols: any
  masterList: any
  httpMessage!: string
  gainers!: PerformanceCard
  losers!: PerformanceCard

  data() {
    return {
      httpMessage: '',
      masterList: [],
      symbols: [],
      gainers: [],
      losers: []
    }
  }

  created() {
    this.httpMessage = 'Fetching data...'
   
    this.stockdb.gainers()
      .pipe(
        mergeMap((res: any) => transformPerformance(res.data))
      )
      .subscribe(
        (result: any) => {
          this.gainers = result.slice()
        },
        (error: Error) => {
          this.httpMessage = error.message
          console.log(error.message)
        }
      )
    
    this.stockdb.losers()
      .pipe(
        mergeMap((res: any) => transformPerformance(res.data))
      )
      .subscribe(
        (result: any) => {
          this.losers = result.slice()
        },
        (error: Error) => {
          console.log(error.message)
        }
      )
  }
}
