



















































import { Component, Vue } from "vue-property-decorator";
import { Subject, from, of, zip } from "rxjs";
import { map, mergeMap, reduce, catchError, tap, toArray, filter, pluck, distinctUntilChanged, debounceTime, take } from "rxjs/operators";
const numeral = require('numeral')
const moment = require('moment')

@Component
export default class Search extends Vue {
  stockdb: any
  symbols: any
  watchlists: any
  masterList: any

  searchSymbol$ = new Subject()
  addWatchList$ = new Subject()

  data() {
    return {
      masterList: [],
      symbols: [],
      watchlists: []
    }
  }

  mounted() {
    zip(this.stockdb.getSymbols(), this.stockdb.getWatchList())
      .subscribe(
        (result: any) => {
          this.masterList = result[0].data.slice()
          
          if (result[1].data) {
            this.watchlists = result[1].data
          }
        },
        (error: Error) => {
          console.log(error)
        }
      )

    this.$subscribeTo(this.searchSymbol$.pipe(
      pluck('event', 'target', 'value'),
      distinctUntilChanged(),
      debounceTime(250),
      mergeMap((input: string) => {
        this.symbols = []

        if (input === '') {
          return of([])
        }

        return from(this.masterList).pipe(
          filter((symbol: any) => {
            const s = symbol.symbol.toLowerCase()
            const n = symbol.name.toLowerCase()
            const i= input.toLowerCase()

            if (s === i) {
              return true
            }

            if (n.includes(i)) {
              return true
            }

            return false
          }),
          take(100),
          toArray()
        )
      }),
    ), (symbols: any) => {
      this.symbols = symbols
    })

    this.$subscribeTo(this.addWatchList$.pipe(
      map((res: any) => {
        const key = res.data.symbol.toLowerCase()

        // toggle watchlist remove / add
        if (this.watchlists[key]) {
          delete this.watchlists[key]
          this.watchlists = Object.assign({}, this.watchlists)
        } else {
          this.watchlists = Object.assign({},  { [key]: res.data }, this.watchlists)
        }

        return res
      }),
      mergeMap(res => this.stockdb.setWatchList(this.watchlists))
    ), (result: any) => {})
  }
}
