



















const moment = require('moment')
import { Component, Prop, Vue } from 'vue-property-decorator';
import { from } from 'rxjs'
import { map, mergeMap, toArray } from 'rxjs/operators'

interface NewsData {
  datetime: string
  headline: string
  source: string
  url: string
  summary: string
  related: string
}

@Component
export default class News extends Vue {
  stockdb: any
  symbol!: string
  news!: NewsData[]

  data() {
    return {
      symbol: this.$route.params.symbol,
      news: [],
    }
  }

  mounted() {
    this.stockdb.getNews(this.symbol).pipe(
      mergeMap((res: any) => from(res.data)),
      map((data: NewsData) => {
        const datetime =  moment(data.datetime)
        data.datetime = `${datetime.fromNow()}, ${datetime.format('lll')}`

        return data
      }),
      toArray()
    )
    .subscribe(
      (result: any) => {
        if (result) {
          this.news = result
        }
      },
      (error: Error) => {
        console.error(error)
      }
    )
  }
}
