





















import { Component, Prop, Vue } from 'vue-property-decorator';

interface EarningData {
  EPSReportDate: string
  EPSSupriseDollar: number
  actualEPS: number
  announceTime: string
  consensusEPS: number
  numberOfEstimates: number
  estimatedChangePercent: number
  estimatedEPS: number
  fiscalPeriod: string
}

@Component
export default class Earnings extends Vue {
  stockdb: any
  symbol!: string
  earnings!: EarningData[]
  error!: string

  data() {
    return {
      symbol: this.$route.params.symbol,
      earnings: [],
      error: 'Not available.'
    }
  }

  created() {
    const baseUrl = 'https://api.iextrading.com/1.0'
    const symbol = this.$route.params.symbol

    this.stockdb.getEarnings(symbol).subscribe(
      (result: any) => {
        if (result.data) {
          this.earnings = result.data.earnings
        }
      },
      (error: Error) => {
        console.error(error)
      }
    )
  }
}
