import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';

import StockHome from './views/stocks/Home.vue'
import StockSearch from './views/stocks/Search.vue'
import StockWatchlist from './views/stocks/Watchlist.vue'
import StockLeaderboard from './views/stocks/Leaderboard.vue'

import CompanyHome from './views/stocks/company/Home.vue'
import CompanyKeyStat from './views/stocks/company/KeyStat.vue'
import CompanyNews from './views/stocks/company/News.vue'
import CompanyChart from './views/stocks/company/Chart.vue'
import CompanyEarnings from './views/stocks/company/Earnings.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/stocks',
      redirect: '/stocks/search',
      component: StockHome,
      children: [
        {
          path: 'search',
          name: 'stocks search',
          component: StockSearch,
        },
        {
          path: 'watchlist',
          name: 'stocks watchlist',
          component: StockWatchlist
        },
        {
          path: 'leaderboard',
          name: 'stocks leaderboard',
          component: StockLeaderboard
        },
        {
          path: 'company/:symbol',
          name: 'stocks company',
          redirect: { name: 'company keystat' },
          component: CompanyHome,
          children: [
            {
              path: 'overview',
              name: 'company keystat',
              component: CompanyKeyStat,
            },
            {
              path: 'news',
              name: 'company news',
              component: CompanyNews,
            },
            {
              path: 'chart',
              name: 'company chart',
              component: CompanyChart,
            },
            {
              path: 'earnings',
              name: 'company earnings',
              component: CompanyEarnings,
            },
          ]
        }
      ]
    },
  ],
});
